import React, { Component } from 'react';

import './card.css';



class Card extends Component {

	constructor(props) {
		super(props);

		this.state = { showModal: false };

		this.onClick = this.onClick.bind(this);
	}

	onClick() {
		console.log("CLICK");
		this.setState((state) => { return { showModal: !state.showModal }});
	}

	getStyle() {
		this.state.showModal ? "card__modal_container show" : "card__modal_container";
	}

	render() {
		const backgroundImage = `url(${this.props.backgroundImage})`;
		return (
			<div className="card" onClick={this.onClick}>
				<div className="card__top">
					<div className="card__description" >
						<div className="card__description_text" >
							{this.props.description}
						</div>
					</div>
					<div
						className="card__cover_photo"
						style={{backgroundImage: backgroundImage}}
					>
					</div>
				</div>
				<div className="card__bottom">

					<div className="card__subtitle">
						{this.props.subtitle}
					</div>
					<div className="card__title">
						{this.props.title}
					</div>
				</div>
				<div className="card__modal_container" >
					<div className="card__modal">
						hello
					</div>
				</div>
			</div>
		);
	}
}

export default Card;



